.path {
  margin-bottom: 8px;
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
}

.path:last-child {
  margin-bottom: 0;
}

.path .destination:nth-child(3) {
  text-align: right;
}

.line {
  display: -ms-flexbox;
  display: flex;
  position: relative;    
  -ms-flex-align: center;    
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.line::before {
  position: absolute;
  content: "";
  left: 16px;
  right: 16px;
  display: block;
  height: 3px;
  background: #3D5AFE;
  border-radius: 2px;
}

.transfer {
  width: 4px;
  height: 4px;
  background: #fff;
  border: 3px solid #3D5AFE;
  border-radius: 50%;
  z-index: 1;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.time {
  font-size: 24px;
}

.airport {
  font-size: 16px;
  color: #999;
}


